/* Styles for the Pop-up */
.achievement {
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #3432cf;
  color: white;
  font-size: 24px;
  padding: 20px 40px;
  border-radius: 18%; 
  z-index: 1000;
  opacity: 1;
  transition: opacity 1s ease-out; 
}

/* Styles for smaller screens */
@media (max-width: 768px) {
  .achievement {
    width: 50%; 
    padding: 15px; 
    font-size: 19px; 
    text-align: center; 
  }
}
